'use client';

import { useState } from 'react';
import { loadUsers, saveUsers, setSession } from '../../lib/auth';

export default function RegisterPage() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');

  async function handleSubmit(e) {
    e.preventDefault();
    const mail = email.trim().toLowerCase();
    if (!mail || !password) {
      alert('Preencha e-mail e senha');
      return;
    }
    const users = loadUsers();
    if (users.find((u) => u.email === mail)) {
      alert('E-mail j치 cadastrado');
      return;
    }
    users.push({ email: mail, password }); // prot칩tipo: sem hash
    saveUsers(users);
    setSession({ email: mail });

    // Chama a API do Next que escreve CSV (substituindo o server.js)
    try {
      await fetch('/api/csv/register', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ email: mail, password }),
      });
    } catch (err) {
      console.warn('Falha ao registrar no CSV:', err);
    }

    window.location.href = '/';
  }

  return (
    <main className="container">
      <section className="hero" aria-label="Cadastro">
        <h2 className="title">Crie sua conta</h2>
        <p className="subtitle">
          Seus dados ficam salvos no seu navegador (somente na sua m치quina).
        </p>

        <form id="formRegister" className="auth-form card-like" onSubmit={handleSubmit}>
          <label className="input">
            <span>游닎</span>
            <input
              name="email"
              type="email"
              placeholder="email@exemplo.com"
              required
              value={email}
              onChange={(e) => setEmail(e.target.value)}
            />
          </label>
          <label className="input">
            <span>游</span>
            <input
              name="password"
              type="password"
              placeholder="Senha"
              required
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
          </label>
          <button className="btn btn-primary" type="submit">
            Criar conta
          </button>
          <p className="muted">
            J치 tem conta? <a href="/login">Entre aqui</a>.
          </p>
        </form>
      </section>
    </main>
  );
}
